@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300,400&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fira+Code:wght@300,400&display=swap);
*,
*::before,
*::after {
    margin: 0;
    box-sizing: border-box;
}

html {
    --ff-text: "Open Sans", sans-serif;
    --ff-code: "Fira Code", monospace;

    --clr-red: #e8064d;
    --clr-white: #d4d4d4;
    --clr-grey: rgb(10, 10, 10);
    --clr-black: black;

    --fs-h1: 3rem;
    --fs-h2: 2.5rem;
    --fs-h3: 1.5rem;
    --fs-body: 1rem;

    height: 100vh;

    overflow-x: hidden;

    -ms-scroll-chaining: none;

        overscroll-behavior: none;
}

body {
    height: 100vh;
    font-family: "Open Sans", sans-serif;
    font-family: var(--ff-text);
    font-size: 1rem;
    font-size: var(--fs-body);
    font-weight: 300;
    color: #d4d4d4;
    color: var(--clr-white);
    background-color: black;
    background-color: var(--clr-black);
}

.App {
    height: 100vh;
    display: flex;
    flex-direction: column;
}

.red {
    color: #e8064d;
    color: var(--clr-red);
}

h1,
h2,
h3 {
    line-height: 1;
    margin: 0;
}

h1 {
    font-size: 3rem;
    font-size: var(--fs-h1);
}

h2 {
    font-size: 2.5rem;
    font-size: var(--fs-h2);
}

h3 {
    font-size: 1.5rem;
    font-size: var(--fs-h3);
}

@media (min-width: 800px) {
    :root {
        --fs-h1: 4.5rem;
        --fs-h2: 3.75rem;
        --fs-h3: 1.8rem;
        --fs-body: 1.25rem;
    }
}

@media (min-width: 1200px) {
    :root {
        --fs-h1: 5.2rem;
        --fs-h2: 4rem;
        --fs-h3: 2rem;
        --fs-body: 1.4rem;
    }
}

@media (min-width: 1500px) {
    :root {
        --fs-h1: 5.5rem;
        --fs-h2: 4.2rem;
        --fs-h3: 2.15rem;
        --fs-body: 1.5rem;
    }
}

@media (min-width: 1750px) {
    :root {
        --fs-h1: 5.75rem;
        --fs-h2: 4.35rem;
        --fs-h3: 2.25rem;
        --fs-body: 1.75rem;
    }
}

@media (min-width: 2000px) {
    :root {
        --fs-h1: 6.1rem;
        --fs-h2: 4.35rem;
        --fs-h3: 2.25rem;
        --fs-body: 1.9rem;
    }
}

@font-face {
    font-family: "Aero";
    src: url(/static/media/aero.364d576d.ttf);
}

a {
    text-decoration: none;
    color: inherit;
}

.header {
    padding: 1.25rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.logo-container {
    display: grid;
    place-items: center;
}

.logo {
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
    font-size: 2rem;
    font-family: "Aero";
}

.nav {
    display: flex;
    font-family: var(--ff-text);
    font-size: 1.5rem;
    font-weight: 400;
}

.nav a {
    text-decoration: none;
    transition: 0.3s ease filter;
    margin: 0.25rem 1rem;
}

.nav a:hover {
    -webkit-filter: brightness(1.25);
            filter: brightness(1.25);
}

i.fa-npm {
    transform: scale(1.5);
}

.bg-circle {
    z-index: -10;
    position: absolute;
    top: -1400px;
    left: 50%;
    transform: translateX(-50%);
    background-color: var(--clr-grey);
    width: 2000px;
    height: 2000px;
    border-radius: 50%;
}

.splash {
    display: grid;
    text-align: center;
    place-items: center;
    min-height: 500px;
}

.splash h2 {
    font-weight: 300;
    margin-bottom: var(--fs-h3);
}

.splash h3 {
    opacity: 0.75;
    font-weight: 300;
}

.fade-1 {
    opacity: 0;
    animation: fadein 0.6s ease forwards;
}

.fade-2 {
    opacity: 0;
    animation: fadein 0.6s 0.2s ease forwards;
}

.fade-3 {
    opacity: 0;
    font-weight: 400;
    animation: fadein 0.6s 0.4s ease forwards;
}

.fade-4 {
    opacity: 0;
    animation: fadein 0.6s 1s ease forwards;
}

.fade-5 {
    opacity: 0;
    animation: fadein 0.6s 1.5s ease forwards;
}

.fade-6 {
    opacity: 0;
    animation: fadein 0.6s 2s ease forwards;
}

.faded {
    pointer-events: none;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 500px;
    mix-blend-mode: difference;
    opacity: 0.15;
    -webkit-user-select: none;
        -ms-user-select: none;
            user-select: none;
}

section {
    flex: 1 1;
}

.links {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
    padding: 2rem;
    height: 100%;
}

@keyframes fadein {
    from {
        opacity: 0;
    }

    to {
        opacity: 1;
    }
}

@media (min-width: 800px) {
    .faded {
        width: 600px;
    }

    .splash {
        min-height: 600px;
    }

    .bg-circle {
        z-index: -10;
        position: absolute;
        top: -3300px;
        left: 50%;
        transform: translateX(-50%);
        background-color: var(--clr-grey);
        width: 4000px;
        height: 4000px;
        border-radius: 50%;
    }

    .links {
        padding: 2rem 4rem;
    }
}

@media (min-width: 1200px) {
    section {
        padding-top: 4rem;
    }

    .links {
        flex-direction: row;
        justify-content: space-between;
    }
}

